// src/components/Weather/DailyWeatherPanel.tsx

import React from 'react';
import { Card, Typography, Row, Col } from 'antd';
import moment from 'moment';
import './DailyWeatherPanel.css';

const { Text } = Typography;

interface WeatherDataEntry {
  datetime: string;
  night?: string;
  morning?: string;
  afternoon?: string;
  evening?: string;
  temperature_high?: number;
  temperature_low?: number;
  precipitation?: number;
  wind_speed?: number;
  wind_direction?: string;
}

interface DailyWeatherPanelProps {
  data: WeatherDataEntry[];
}

const DailyWeatherPanel: React.FC<DailyWeatherPanelProps> = ({ data }) => {
  return (
    <div className="daily-weather-panel">
      {data.map((entry, index) => (
        <Card key={index} className="weather-card">
          <Row gutter={16}>
            <Col span={4}>
              <Text strong>{moment(entry.datetime).format('dddd D MMM')}</Text>
            </Col>
            <Col span={2}>
              <div className="weather-icon">
                {/* Placeholder for the weather icon for Night */}
                <Text>{entry.night}</Text>
              </div>
            </Col>
            <Col span={2}>
              <div className="weather-icon">
                {/* Placeholder for the weather icon for Morning */}
                <Text>{entry.morning}</Text>
              </div>
            </Col>
            <Col span={2}>
              <div className="weather-icon">
                {/* Placeholder for the weather icon for Afternoon */}
                <Text>{entry.afternoon}</Text>
              </div>
            </Col>
            <Col span={2}>
              <div className="weather-icon">
                {/* Placeholder for the weather icon for Evening */}
                <Text>{entry.evening}</Text>
              </div>
            </Col>
            <Col span={3}>
              <Text>
                {entry.temperature_high}° / {entry.temperature_low}°
              </Text>
            </Col>
            <Col span={3}>
              <Text>{entry.precipitation} mm</Text>
            </Col>
            <Col span={3}>
              <Text>
                {entry.wind_speed} m/s {entry.wind_direction}
              </Text>
            </Col>
            <Col span={3}>
              <Text className="forecast-link">Open hourly forecast</Text>
            </Col>
          </Row>
        </Card>
      ))}
    </div>
  );
};

export default DailyWeatherPanel;
